
import { computed, defineComponent, onMounted, ref } from 'vue'
import {
    IonPage,
    IonContent,
    IonToolbar,
    IonHeader,
    IonFooter,
    IonTitle,
    IonList,
    IonItem,
    IonText,
    IonLabel,
    IonButton,
    loadingController
} from "@ionic/vue"
import ReportTable from "@/components/DataViews/tables/ReportDataTable.vue"
import table from "@/components/DataViews/tables/ReportDataTable"
import { MergingService, MergeHistory } from "@/services/merging_service"
import { Option } from '@/components/Forms/FieldInterface'
import { useRouter } from 'vue-router'
import { alertConfirmation, toastDanger, toastSuccess, toastWarning } from '@/utils/Alerts'
import HisDate from "@/utils/Date"
import { MultiStepPopupForm } from "@/utils/PopupKeyboard"
import { FieldType } from '@/components/Forms/BaseFormElements'
import { isEmpty } from 'lodash'
import { tableActionSheet } from "@/utils/ActionSheets"

export default defineComponent({
    components: {
        IonText,
        IonPage,
        IonContent,
        IonToolbar,
        IonHeader,
        IonFooter,
        IonTitle,
        IonList,
        IonItem,
        IonLabel,
        IonButton,
        ReportTable
    },
    setup() {
        const npid = ref('')
        const patientID = ref(-1)
        const router = useRouter()
        const rows = ref([] as any)
        const results = ref([] as MergeHistory[])
        const holderName = ref('N/A' as string)
        const patientInfo = ref([
            { label: 'Birthdate', value: 'N/A'},
            { label: 'Gender', value: 'N/A'}
        ] as Option[])
        const columns = [
            [
                table.thTxt('Merge #'),
                table.thTxt('Winner'),
                table.thTxt('Loser'),
                table.thTxt('Merge date'),
                table.thTxt('Merge type'),
                table.thTxt('View'),
                table.thTxt('Reverse')
            ]
        ]

        const cancel = () => router.back()
    
        async function rollback(secondaryID: number) {
            if ((await alertConfirmation('Are you sure you want to reverse this merge?'))) {
                try {
                    (await loadingController.create({
                        message: 'Please wait....',
                        backdropDismiss: false
                    })).present()
                    if ((await MergingService.rollback(secondaryID))) {
                        toastSuccess('Merge successfully reversed')
                        loadingController.dismiss() 
                        return true
                    } else {
                        loadingController.dismiss()
                        toastDanger('Failed to rollback')
                        return false
                    }
                } catch (e) {
                    loadingController.dismiss()
                    console.error(e)
                    toastDanger(`${e}`)
                }
            }
        }
    
        function scanPatient() {
            MultiStepPopupForm([{
                id: 'npid',
                helpText: 'Scan patient NPID',
                type: FieldType.TT_BARCODE,
                onValue: async (scanned: string) => {
                    if (!isEmpty(results.value) && scanned === npid.value) {
                        return true
                    }
                    npid.value = scanned
                    if (!scanned) {
                        toastWarning('Invalid barcode')
                        return false
                    }
                    try {
                        results.value = (await MergingService.getMergeHistory(scanned) || [])
                        rows.value = results.value.map((item: MergeHistory, index: number) => {
                            if (patientID.value === -1) {
                                patientID.value = item['secondary_id']
                                holderName.value =`${item['secondary_first_name']} ${item['secondary_surname']}`
                                patientInfo.value = [
                                    { label: 'Birthdate', value: HisDate.toStandardHisDisplayFormat(
                                        item['secondary_birthdate']
                                    )},
                                    { label: 'Gender', value: item['secondary_gender']},
                                ]
                            }
                            return [
                                table.td(item['merge_number']),
                                table.td(`${item['primary_first_name']} ${item['primary_surname']}`),
                                table.td(`${item['secondary_first_name']} ${item['secondary_surname']}`),
                                table.tdDate(item['merge_date']),
                                table.td(item['merge_type']),
                                table.tdBtn('View', async () => {
                                    await tableActionSheet(
                                        `Merge # ${item['merge_number']}`,
                                        'Client merge information',
                                        ['Attribute', 'Winner', 'Loser'],
                                        [
                                            [ 'First name', item['primary_first_name'], item['secondary_first_name'] ],
                                            [ 'Last name', item['primary_surname'], item['secondary_surname'] ],
                                            [ 'Birthdate', item['primary_birthdate'], item['secondary_birthdate']],
                                            [ 'Gender', item['primary_gender'], item['secondary_gender']]
                                        ],
                                        [
                                            { 
                                                name: 'Close',
                                                slot: 'start', 
                                                color: 'primary'
                                            }
                                        ]
                                    )
                                }, {},'success'),
                                table.tdBtn('Reverse', async () => {
                                    if ((await rollback(item['secondary_id']))) {
                                        rows.value = rows.value.filter((r: any) => r[0].td != item['merge_number'])
                                        if (isEmpty(rows.value)) {
                                            results.value = []
                                            scanPatient()
                                        }
                                    }
                                })
                            ]
                        })
                    } catch (e) {
                        results.value = []
                        rows.value = []
                        toastWarning(`${e}`)
                        return false
                    }
                    return true
                },
                config: {
                    hiddenFooterBtns: ['Next', 'Clear']
                }
            }], (f: any) =>  console.log(f), () => isEmpty(results.value) && cancel())
        }

        onMounted(() => scanPatient())

        return {
            cancel,
            scanPatient,
            holderName,
            patientInfo,
            columns,
            rows,
            npid
        }
    }
})
